.experience{
    padding: 70px;
    align-items: center;
}

.experience-div{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0 2rem;
    align-items: center;
    justify-content: space-around;
    padding: 30px;
    font-family: 'Epilogue', sans-serif;
    
}

.exp-div{
    display: flex;
    margin:-8px;
    padding: 0;
    height: 250px;
}

.exp-date{
    border-right: 1px solid #808080;
    width: 150px;
    padding-right: 15px;
    text-align: right;
    color: #808080;
    padding-top: 25px;
}

.exp-data{
    display: flex;
    flex-direction: column;
    width: 400px;
    padding: 25px 0;
    padding-left: 15px;
}

.exp-designation{
    color: #383737;
    font-size: 30px;
    font-weight: 700;
    padding-bottom: 15px;
}

.exp-description{
    color: #2D2D2D;
    line-height: 30px;
    padding-right: 150px;
}

.exp-company{
    padding-bottom: 15px;
}

.exp-company a{
    text-decoration: none;
    font-weight: 700;   
}

.exp-company a:hover{
    text-decoration: underline;
    transition: 4s ease;
}

.persistent{
    color: #FD5F07;
}

.inkflix{
    color: #FE3231;
}

.lendahand{
    color: #8A4700;
}

.ufaber{
    color: #3498DB;
}

@media screen and (max-width:1100px){
    .experience-div{
        display: flex;
        flex-direction: column;
    }

    .exp-description{
        padding-right: 0;
    }
}

@media screen and (max-width:768px){
    .experience-div{
        display: flex;
        flex-direction: column;
    }

    .exp-description{
        padding-right: 0;
    }

    .exp-date{
        width: 100px;
    }

    .exp-data{
        width: 200px;
    }

    .exp-div{
        height: 300px;
    }
}