.footer-div{
    padding: 30px 0 70px 0;
    background-color: rgba(14,110,63,0.51);
    display: flex;
    flex-direction: column;
    align-items: center;
}

.footer-div h2{
    color: #367153;
}



.footer-icons{
    display: flex;
    justify-content: space-around;
    width: 50%;
}

@media screen and (max-width: 500px){
    .footer-icons img{
        width: 70%;
    }

    .footer-div h2{
        font-size: 25px;
        
    }
}