.latest-work-div{
    padding: 30px;
}

.work-elements{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items:center;
}

.project{
    margin: 30px 0;
}

.description{
    display: flex;
    justify-content: space-between;
}

.description .work-icons{
    display: flex;
    justify-content: center;
}

.work-icons a{
    padding-right: 30px;
}

.work-elements h3{
    font-size: 30px;
    padding-bottom: 10px;
    color: #67D5AD;
}

.work-img{
    text-align: left;
    padding-top: 30px;
}

.work-img img{
    border-radius: 12px;
    width: 95%;
}

@media screen and (max-width: 1100px){

    .work-icons a{
        padding-right: 10px;
    }

}