.Certifications{
    padding: 70px;
    align-items: center;
    background-color: #FAFAFA;
}


.certifications-div{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0 2rem;
    align-items: center;
    justify-content: space-around;
    padding: 30px;
    font-family: 'Epilogue', sans-serif;
    
}

.cert-div{
    display: flex;
    margin:-8px;
    padding: 0;
    height: 100px;
}

.cert-date{
    border-right: 1px solid #808080;
    width: 150px;
    padding-right: 15px;
    text-align: right;
    padding-top: 30px;
    color: #808080;
}

.cert-name{
    display: flex;
    flex-direction: column;
    width: 500px;
    padding: 25px 0;
    padding-left: 15px;
    padding-right: 100px;
}

.cert-name a{
    text-decoration: none;
    font-size: 30px;
    font-weight: 400;
    color: #2D2D2D;
}

.cert-name a:hover{
    text-decoration: underline;
}


@media screen and (max-width:1200px){
    .certifications-div{
        display: flex;
        flex-direction: column;
    }

    .cert-name{
        padding-right: 0;
        font-size: 10px;
    }

    .cert-date{
        width: 70px;
    }
}

@media screen and (max-width:768px){
    .certifications-div{
        display: flex;
        flex-direction: column;
    }

    .cert-date{
        width: 100px;
    }

    .cert-name{
        width: 200px;
        padding-right: 0;
        font-size: 10px;
    }

    .cert-div{
        height: 200px;
    }
}