

.banner-div{
    height: 100vh;
    background-color: #fff;
    font-family: 'Epilogue', sans-serif;
    color: #2D2D2D;
    display: flex;
    justify-content: space-between;
    padding: 0 100px;
}


.job{
    padding-top: 100px;
    padding-bottom: 80px;
}

.job h3{
    font-size: 35px;
}

.name{
    padding-bottom: 30px;
}

.data{
    padding-top: 10px;
    font-weight: 200;
    padding-bottom: 50px;
}

.banner-div h1{
    font-size: 90px;
}

.banner-div a{
    padding: 0 45px;
}

.banner-icons{
    display: flex;

}



@media screen and (max-width: 1300px){

    .job{
        padding: 30px 0;
    }

}
@media screen and (max-width: 768px){

    

    .Banner{
        display: flex;
        align-items: center;
        justify-content: space-around;
        padding: 50px;
    }

    .banner-div{
        display: flex;
        flex-direction: column;
        padding: 0;
        justify-content: center;
    }
    
    
    .job{
        padding-bottom: 20px;
    }
    
    .job h3{
        font-size: 20px;
    }
    
    .name{
        padding-bottom: 0;
    }
    
    .data{
        padding-top: 10px;
        font-weight: 200;
        padding-bottom: 20px;
    }
    
    .banner-div h1{
        font-size: 90px;
    }
    
    .banner-div a{
        padding: 0 20px;
    }

    .banner-img{
        width: 100%;
        text-align: center;
    }

    .banner-img img{
        width: 80%;
    }
}

@media screen and (max-width: 768px){
    .banner-icons img{
        width: 80%;
    }

}