.line-1, .line-2{
    white-space: nowrap;
    overflow: hidden; 
    border-right: 2px solid #FF6250;
    padding:10px 0;  
}

.name-anim-div h1{
    font-family: monospace;
    font-weight: 400;
}


.line-1{
    width: 5em;
}


.line-2{
    width: 7.6em;
}



/* Animation */
.anim-typewriter-1{
  animation: typewriter-1 1.5s ease 1s 1 normal both, blinkTextCursor 500ms steps(44) 1.5s normal; 
}


.anim-typewriter-2{
    
    animation: typewriter-2 1.6s ease 1s 1 normal both, blinkTextCursor 500ms steps(44) 1.6s normal; 
  }


.delay{
    animation-delay: 3s;
}

@keyframes typewriter-1{
  from{width: 0;}
  to{width: 5.1em;}
}

@keyframes typewriter-2{
    from{width: 0;}
    to{width: 7.6em;}
  }

@keyframes blinkTextCursor{
    from {border-right-color: #FF6250;}
    to {border-right-color: transparent;}
}

.small-screen{
  display: none;
}


@media screen and (max-width: 1300px){

  .large-screen{
    display: none;
  }

  .small-screen{
    display: block;
  }

  p.small-screen{
    font-size: 35px;
  }

  p.line-2{
    width: 8em;
  }

  @keyframes typewriter-2{
    from{width: 0;}
    to{width: 8.1em;}
  }
}